@import url(https://fonts.googleapis.com/css2?family=Vibur&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.home{height:calc(100vh - 66px);background-image:url(/cursor-react-routes/static/media/home_bg.08aa73b5.jpg);background-size:cover;background-position:center;background-repeat:no-repeat;display:flex;align-items:center;justify-content:center}.home__title{font-size:20vw;text-align:center;line-height:1;font-family:'Vibur', cursive;color:#c6e2ff;-webkit-animation:neon .08s ease-in-out infinite alternate;animation:neon .08s ease-in-out infinite alternate}@-webkit-keyframes neon{from{text-shadow:0 0 6px rgba(202,228,225,0.92),0 0 30px rgba(202,228,225,0.34),0 0 12px rgba(30,132,242,0.52),0 0 21px rgba(30,132,242,0.92),0 0 34px rgba(30,132,242,0.78),0 0 54px rgba(30,132,242,0.92)}to{text-shadow:0 0 6px rgba(202,228,225,0.98),0 0 30px rgba(202,228,225,0.42),0 0 12px rgba(30,132,242,0.58),0 0 22px rgba(30,132,242,0.84),0 0 38px rgba(30,132,242,0.88),0 0 60px #1e84f2}}@keyframes neon{from{text-shadow:0 0 6px rgba(202,228,225,0.92),0 0 30px rgba(202,228,225,0.34),0 0 12px rgba(30,132,242,0.52),0 0 21px rgba(30,132,242,0.92),0 0 34px rgba(30,132,242,0.78),0 0 54px rgba(30,132,242,0.92)}to{text-shadow:0 0 6px rgba(202,228,225,0.98),0 0 30px rgba(202,228,225,0.42),0 0 12px rgba(30,132,242,0.58),0 0 22px rgba(30,132,242,0.84),0 0 38px rgba(30,132,242,0.88),0 0 60px #1e84f2}}

.post{display:grid;grid-template-columns:60px 1fr;max-width:100%;position:relative;color:#fff}.post__show__icon{position:absolute;top:5px;right:0;color:#888}.post__logo{width:100%}.post__logo img{border-radius:50%;width:100%}.post__content{margin-left:10px}.post__img img{width:100%;border-radius:25px}.post__text{margin-bottom:10px}.post__status{padding:15px 10px;color:#888;display:flex}.post__status div{width:25%}.post__icon{margin-right:10px}.post__author{display:flex;margin-top:5px}.post__author__name{font-weight:bold;font-size:20px}.post__author__name-icon{margin-left:5px}.post__author__nick-name{margin-left:10px;color:#888}

.posts__wrapp{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px;padding:25px}

.photos{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, 1fr);height:calc(100vh - 66px);overflow:hidden;position:relative}.photos__img{width:100%;height:100%;object-fit:cover;object-position:center;transition:all .3s ease;position:relative}.photos__img:hover{transform:scale(1.3) rotate(3deg);z-index:10}

.contact{display:flex;margin-top:7px;padding:5px}.contact__wrap{padding:0 7px}.contact:hover{background-color:rgba(136,136,136,0.2)}.contact__clear{text-align:center;margin-top:20px}.contact__gender{width:40px;height:40px;display:flex;align-items:center;justify-content:center;border-radius:50%;line-height:1}.contact__gender.male{background-color:#63b4e3}.contact__gender.female{background-color:#e36398}.contact__gender.n-a{background-color:#cc7b7b}.contact__info{display:flex;flex-direction:column;justify-content:center;margin-left:10px}.contact__name{font-size:17px;color:#fff}.contact__phone a{color:#888;font-size:14px;text-decoration:none}

.contacts{width:320px;height:430px;margin:auto;background-color:rgba(51,51,51,0.3)}.contacts__search{background-color:#0070e0;height:40px;display:flex;align-items:center;justify-content:space-between;padding:0 10px}.contacts__search input{background-color:transparent;color:#fff;border:none;font-size:16px;width:100%;margin-right:10px}.contacts__search input:focus{outline:none}.contacts__search input::-webkit-input-placeholder{color:#fff}.contacts__search input:-ms-input-placeholder{color:#fff}.contacts__search input::-ms-input-placeholder{color:#fff}.contacts__search input::placeholder{color:#fff}.contacts__checkbox{display:flex;align-items:center}.contacts__checkbox__wrap{display:flex;justify-content:space-around;background-color:#034765;padding:10px}.contacts__checkbox input{display:none}.contacts__checkbox label{padding:5px 15px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none}.contacts__checkbox input:checked+label{background-color:rgba(0,112,224,0.2);color:#fff}

.contact--page{height:calc(100vh - 66px);display:flex;align-items:center;justify-content:center}

.contact-detail{color:#999;height:calc(100vh - 66px);display:flex;align-items:center;justify-content:center}.contact-detail__wrap{border:1px solid #999;padding:25px;border-radius:5px}.contact-detail__item{display:flex;justify-content:space-between;min-width:200px}.contact-detail__item:not(:last-of-type){margin-bottom:5px}.contact-detail__name{font-weight:bold}

.menu{box-shadow:0px 1px 10px 0px rgba(51,51,51,0.5);background-color:rgba(51,51,51,0.5);text-align:center;padding:25px 10px}.menu__items{display:flex;max-width:550px;width:100%;justify-content:center;margin:auto}.menu__item{color:#888}.menu__item:not(:last-of-type){margin-right:15px}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
*{
	box-sizing: border-box;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	color: inherit;
    text-decoration: none;
}
body{background-color:#222;font-family:'Roboto', sans-serif}

